.board{
  width: min-content;
  display: flex;
  align-items: flex-start;
}
.addButton{
  width: 300px;
  height: 3em;
  background-color: #b1b1b1;
  color: #fff;
  border: 0;
  border-radius: 1em;
  font-weight: bold;
  cursor: pointer;
}

.addButton:hover{
  background-color: #9e9e9e;
}